import { useRouter } from 'next/router';
import Layout from '../../components/MyLayout';
import CarList from '../../components/Lists/CarList';
import ApiBasicUrl from '../../configuration/model/apiSettings';
import fetch from 'isomorphic-unfetch'
import MainBar from '../../components/MainBar';

const Post = (props) => {
  const router = useRouter();

  if(router.query.id === "car")
  {
    return (
      <Layout title="Cars">
      <CarList cars={props.cars}/>
    </Layout>
    )
  }

  return (
    <Layout title="Catalogs">
      {props.cars}
    </Layout>
  );
}

Post.getInitialProps = async () => {
  const res = await fetch(ApiBasicUrl().concat(`/${router.query.id}`));
  const data = await res.json();
  console.log(data);
  return {
    cars: data
  };
};

export default Post